  .week {
    display: flex;
  }
  
  .day {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0;
  }
  
  html, body {
    height: 100%;
  }
   
  .month {
    max-width: 600px;
    margin: auto;
  }
  
  .week {
    height: 80px;
  }
  
  .day {
    color: yellowgreen;
    padding: 5px;
    background-color: rgba(255, 255, 255, 0.137);
    box-shadow: 
      -1px -1px yellowgreen, 
      inset -1px -1px 0 0 yellowgreen;
  }