/*geral*/

body {
    overflow-y: scroll;
    background-color: darkgreen;
}

/* containers */

.container-painel-back{
    width: 600px;
    margin: auto; 
    height: 100vh;
    background-color: whitesmoke;
}

.menu-top-painel{
    display: flex;
    flex-wrap: nowrap;
    position: sticky;
    width: 600px;
    padding: 3px;
    height: 60px;
    background-color: darkgreen;
}

.nav-top{
    display: flex;
    flex-wrap: nowrap;
    position: sticky;
    width: 600px;
    height: 41px;
    align-items: center;
}

.menu-botton-painel{
    display: flex;
    position: relative;
    width: 600px;
    padding: 3px;
    bottom: 0px; 
    height: 56px;
    background-color: whitesmoke;
    
}

@media (max-width: 600px) {
    .container-painel-back, .menu-top-painel, .nav-top, .menu-botton-painel{   
        width: 100%;
    }
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

.boddy-painel{
    position: relative;
    text-align: center;
    height: min-content;
    width: 100%;
    background-color: whitesmoke;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    animation: fadein 1s;
}

/* images */

.img-usuario {
    background-color: yellowgreen;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: relative;
}
  
.img-usuario img {
    position: absolute;
    bottom: 0;
    width: 100%;
}

.btn-img-botton{
    width: 20%;
    text-align: center;
    opacity: 0.5;
}

.btn-img-botton:hover{
    opacity: 1;
}

.btn-img{
    width: 20%;
    text-align: center;
    opacity: 1;
}

/* textos */

.titulo-painel{
    color: yellowgreen;
    width: 100%;
    text-align: center;
    margin-top: 5px;
}

.nav-titulo{
    margin-top: 3px;
    text-align: center;
    color: black;
}

.img-especialista {
    background-color: yellowgreen;
    border-radius: 50%;
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: relative;
  }
  
.img-especialista img {
    position: absolute;
    bottom: 0;
    width: 100%;
  }

.text-especialidade{
    line-height: 0.1;    
}

.list-group{
    margin: 5%;
}

/* outros */

.margem-botton-100{
    height: 100px;
}
