/***************************ATENDIMENTOS*******************************/
/*containers*/

.container-atendimento{
    background-color: papayawhip;
    border: silver solid 0.5px;
    height: max-content;
    border-radius: 15px;
    box-shadow: 1px 1px 3px silver;
    margin-bottom: 10px;
}

.container-atendimento-opacity{
    opacity: 0.5;
    background-color: papayawhip;
    border: silver solid 0.5px;
    height: max-content;
    border-radius: 15px;
    box-shadow: 1px 1px 3px silver;
    margin-bottom: 10px;
}

.col-atendimento-hora{
    color: darkgreen;
    display: flex;
    font-size: 16px;
    align-items: center;
    line-height: 0.2;
    margin-top: 15px;        
}

.col-atendimento-detalhe{
    text-align: left; 
}

.col-atendimento-btn{
    display: flex;
    align-items: center;    
}

/* bot√µes e textos*/

.tit-atendimento-detalhe{
    color: darkgreen;
    font-size: 18px;
    line-height: 0.2;
    margin-top: 15px;
    margin-bottom: 23px;
}

.des-atendimento-detalhe{
    font-size: 11px;
    line-height: 0.1;
}

