/*containers*/

.container-cliente{
    background-color: whitesmoke;
    border: silver solid 0.5px;
    height: max-content;
    border-radius: 15px;
    box-shadow: 1px 1px 3px silver;
    margin-bottom: 10px;
}

.col-cliente-detalhe{
    text-align: left; 
}

.col-cliente-btn{
    display: flex;
    align-items: center;    
}

/* bot√µes e textos*/

.tit-cliente-detalhe{
    color: darkgreen;
    font-size: 20px;
    line-height: 0.2;
    margin-top: 15px;
    margin-bottom: 23px;
}

.des-cliente-detalhe{
    font-size: 12px;
    line-height: 0.2;
}
