/*geral*/

body {
    overflow-y: scroll;
    background-color: darkgreen;}

/* containers */

.container-painel{
    width: 600px;
    padding: 0px;
    margin: auto; 
    height: 100vh;
    background-image: linear-gradient(darkgreen, darkgreen,darkgreen,darkgreen,
    whitesmoke,whitesmoke,whitesmoke,
    whitesmoke,whitesmoke,whitesmoke,
    whitesmoke,whitesmoke,whitesmoke,
    whitesmoke,whitesmoke,whitesmoke,
    whitesmoke,whitesmoke,whitesmoke,
    whitesmoke,whitesmoke,whitesmoke,
    whitesmoke,whitesmoke,whitesmoke);
}

.menu-top-painel{
    display: flex;
    flex-wrap: nowrap;
    position: sticky;
    width: 600px;
    padding: 3px;
    height: 46px;
    background-color: darkgreen;
}

.nav-top-painel{
    display: flex;
    flex-wrap: nowrap;
    position: sticky;
    width: 600px;
    padding: 3px;
    height: 41px;
    align-items: center;
}

.menu-botton-painel{
    display: flex;
    flex-wrap: nowrap;
    position: fixed;
    width: 600px;
    padding: 3px;
    bottom: 0px; 
    height: 56px;
    background-color: whitesmoke;
}

@media (max-width: 600px) {
    .container-painel, .menu-top-painel, .nav-top-painel, .menu-botton-painel{   
        width: 99%;
    }
}

.boddy-painel{
    position: relative;
    text-align: center;
    height: min-content;
    width: 100%;
    background-color: whitesmoke;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.form-field{
    margin-top: 20px;
}

/* images */

.img-usuario{
    display: flex;
    position: absolute;
    right: 0px;
    margin: 15px;
    width: 50px;
    height: 50px;
}

.btn-img-botton{
    width: 20%;
    text-align: center;
    opacity: 0.5;
}

.btn-img-botton:hover{
    opacity: 1;
}

.btn-img{
    width: 20%;
    text-align: center;
    opacity: 1;
}

/* textos e bot√µes */

.titulo-painel{
    color: yellowgreen;
    width: 100%;
    text-align: center;
    margin-top: 5px;
}

.nav-titulo-painel{
    margin-top: 3px;
    text-align: center;
    color: whitesmoke;
}

.text-form{
    line-height: 0.1;
}
